<template>
  <div>
      <div class="container">
        <h1>Escuadrón</h1>
      </div>
    <table-squadron-component
        @addSquadron="addSquadron"
        @updateSquadron="updateSquadron"
        @DeleteSquadron="DeleteSquadron"
        :dataSquadron="SquadronListState"/>
  </div>

</template>

<script>
import tableSquadronComponent from "@/components/squadron/tableSquadronComponent";
import {mapActions, mapGetters} from 'vuex'
export default {
  name: "SquadronComponent",
  components: {
    tableSquadronComponent
  },
  data () {
    return {
      dataSquadron: [],
      jsonDataUpdate: {
        id: 0,
        abbreviation: '',
        code: '',
        description: '',
        externalCode: '',
        features: '',
        nominativo: '',
        photo: '',
        repartoId: '',
        status: '',
        timeTamp: '',
        type: 0,
      }
    }
  },
  mounted() {
    this.getSquadronState()
  },
  computed: {
    ...mapGetters(['SquadronListState'])
  },
  methods: {
    ...mapActions(['getSquadronState', 'setSquadronStatenew', 'apiUpdateSquadron', 'apiDeleteSquadron']),

    addSquadron( item) {
      this.setSquadronStatenew(item)
    },
    updateSquadron( item) {
      this.jsonDataUpdate.abbreviation = item.abbreviation;
      this.jsonDataUpdate.code = item.code;
      this.jsonDataUpdate.description = item.description;
      this.jsonDataUpdate.externalCode = item.externalCode;
      this.jsonDataUpdate.features = item.features;
      this.jsonDataUpdate.nominativo = item.nominativo;
      this.jsonDataUpdate.photo = item.photo;
      this.jsonDataUpdate.repartoId = item.repartoId;
      this.jsonDataUpdate.status = item.status;
      this.jsonDataUpdate.timeTamp = item.timeTamp;
      this.jsonDataUpdate.type = item.type;
      this.jsonDataUpdate.id= item.id;
      this.apiUpdateSquadron(this.jsonDataUpdate)
    },
    DeleteSquadron(item) {
      this.apiDeleteSquadron(item)
    }
  }
}
</script>

